import React, { useState, useEffect } from 'react';
import quoteList from './quotes.json'
import { Button } from 'react-bootstrap';
import {BsTwitter} from 'react-icons/bs'
import {BsChatRightQuoteFill} from 'react-icons/bs'

const App = () => {

  const quotes = quoteList.quotes;

  const [quote, setQuote] = useState('')
  const [author, setAuthor] = useState('')
  const [transparent, setTransparent] = useState(false)

  useEffect(() => {
    const randomIndex = Math.floor(Math.random() * quotes.length);
    setQuote(quotes[randomIndex].quote);
    setAuthor(quotes[randomIndex].author);
  }, [quotes]);

  const handleClick = () => {
    setTransparent(true);
    const randomIndex = Math.floor(Math.random() * quotes.length);
    setTimeout(() => {
      setQuote(quotes[randomIndex].quote);
      setAuthor(quotes[randomIndex].author);
      setTransparent(false);
    }, 500);
  }

  return (
    <div className='container-fluid justify-content-center vh-100 d-flex flex-column bg-primary'>

    
      <div  id="quote-box" className='container p-5 bg-info justify-content-center border border-dark border-5 rounded'>
        <h1 className='row' >
        <div className='font-custom display-5 text-dark border border rounded-pill p-3 mb-5 border-dark'>Quote Machine</div>
          <div className={`${transparent ? 'text-transparent' : 'text-show'} container`}>
            <div className='font-bold text-light' id="text">
              {`"${quote}"`}
            </div>

            <div className='mt-5 text-light font-italic' id="author">
              -  {author}
            </div>
          </div>
        </h1>

        <h1 className='row mt-5'>
          <div className='container'>
            <Button className='border border-dark border-3 rounded-pill bg-success' onClick={handleClick} id="new-quote">
              <BsChatRightQuoteFill />
            </Button>

            <a id="tweet-quote" href="twitter.com/intent/tweet">
              <BsTwitter />
            </a>
          </div>
        </h1>

      </div>
    </div>
  );
}

export default App;
